import { useState, useEffect } from "react";
import Storage from "../utils/storage";
const useWeather = () => {
  const [today, setToday] = useState({});
  const [later, setLater] = useState([]);
  const weather = Storage.get("weather") || {};
  useEffect(() => {
    if (weather && weather.date && new Date(weather.date).toDateString() === new Date().toDateString()) {
      setToday(weather.today);
      setLater(weather.later);
      return;
    };
    window._AMapSecurityConfig = {
      securityJsCode: '8e6fb7b12ca0f8e9093fec79597d3388',
    }
    window.onLoad = function () {
      window.AMap.plugin('AMap.Weather', function () {
        //
        let weatherAPI = new window.AMap.Weather();
        Promise.all([
          new Promise(res => {
            //
            weatherAPI.getLive('', function (err, data) {
              if (!err) {
                weather.today = data;
                setToday(data);
              } else {
                console.log(err);
              }
              res();
            });
          }),
          new Promise(res => {
            weatherAPI.getForecast('', function (err, data) {
              if (!err) {
                weather.later = data;
                setLater(data);
              }
              res();
            });
          })
        ]).then(() => {
          weather.date = +new Date();
          Storage.set("weather", weather);
        })


      });
    }
    var url = 'https://webapi.amap.com/maps?v=1.4.15&key=46dc74aebed689e7586325ef13585c27&callback=onLoad';
    var jsapi = document.createElement('script');
    jsapi.setAttribute("charset", "utf-8");
    jsapi.src = url;
    document.head.appendChild(jsapi);
    return () => window.onLoad = null;

    // eslint-disable-next-line
  }, []);
  return { today, later }
}

export default useWeather;
